* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
#root {
	display: flex;
	justify-content: center;
	font-family: 'Mukta', sans-serif;
}
body{
	height: 220px;
	background-color: rgb(243, 243, 243);
	background-image: url(..//bg-desktop-light.jpg);
	background-size:cover;
	background-repeat: no-repeat;
	font-family: 'Josefin Sans', sans-serif;
}
.App {
	min-width: 800px;
	margin: 30px auto;
}
.wrapper{
	max-width: 540px;
	margin: 0 auto;
}
.post {
	border-top: 1px  hsl(236, 33%, 92%) solid ;
	margin: 0 auto;
	background-color: hsl(0, 0%, 98%);
	max-width: 540px;
	display: flex;	
}
.myInput{
	width: 88%;
	font-size: 20px;
	padding: 18px 0;
    outline:none;
	border: none;
	color: hsl(235, 19%, 35%);
}
h1,h2{
	font-weight: 500;
}
.post-act{
	background-color: red;
	display: none;
}
.todo__create{
	border-radius: 5px;
	display: flex;	
	margin-bottom: 25px;
}
.todo__create-checkbox{
	border-radius: 30px;
	width: 25px;
	padding: 1px;
	margin: auto 20px;
	height: 25px;
	border-radius: 50%;
	background-color: rgb(202, 202, 202);
}
.todo__create-checkbox:hover{
	background: linear-gradient(180deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
	height: 25px;
	width: 25px;
}
.todo__create-checkbox-child{
	background-color: hsl(0, 0%, 98%);;
	border-radius: 50%;
	height: 23px;
	width: 23px;
}
.active{
	border-radius: 50%;
	width: 25px;
	height: 25px;
	background: linear-gradient(45deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}
.active img{
	border-radius: 50%;
	margin: 3px;
}
.paragraph{
	padding: 18px 0 ;
	color: hsl(235, 19%, 35%);
	font-size: 20px;
}
.paragraph__active{
	text-decoration: line-through;
	color: rgb(202, 202, 202);
}
.task{
	padding: 0 0 ;
	font-size: 18px;
	margin-top: 25px;
	align-items: flex-end;
}
.post:first-child{
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
}
.inputDiv{
	display: flex;
}
.todo__header{
	display: flex;
	justify-content: space-between;
	max-width: 540px;
	margin: 0 auto;
}
.todo__header-title{
	letter-spacing: 20px;
	font-size: 38px;
	color: hsl(236, 33%, 92%);;
	margin-bottom: 40px;
}
.todo__header-icon{
	height: 28px;
	margin-top: 3px;
}
.footer{
	display: flex;
	justify-content: space-between;
	background-color:hsl(0, 0%, 98%);
	border-top: 1px  hsl(236, 33%, 92%) solid ;
	border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
	padding: 18px 20px;
	color: hsl(235, 19%, 35%);
	font-size: 14px;
}
.footer__buttons{
	gap: 10px;
	display: flex;
}
.footer__text{
	color:  rgb(202, 202, 202);
}
.footer__button{
	background-color: hsl(0, 0%, 98%);
	border: none;
	color:  rgb(202, 202, 202);
	cursor: pointer;
}
.footer__button:hover{
	color:  hsl(235, 19%, 35%);
}

@media (max-width: 768px) { 
	.post{
		max-width: 350px;
	}
	.footer{
		max-width: 350px;
		margin: 0 auto;
	}
	.App{
		min-width: 0;
	}
}